import React from 'react';
import Container from 'react-bootstrap/Container';
import dictionaryImg from '../../assets/dictionary.png';
import styles from './dictionary.module.css';
import DictionarySection from '../../components/DictionarySection/DictionarySection';

const Dictionary = (): JSX.Element => (
  <Container fluid>
    <h4></h4>
    <img className={styles.dictionaryImg} src={dictionaryImg} alt="  " />
    <hr className={styles.color} />
    <DictionarySection url="difficult" text=" " />
    <hr className={styles.color} />
    <DictionarySection url="deleted" text=" " />
  </Container>
);

export default Dictionary;
