import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import FigureItem from '../../components/FigureItem/FigureItem';
import Games from '../Games/Games';
import mainScreenImg from '../../assets/main_screen.png';
import booksImg from '../../assets/icons/books.svg';
import gameImg from '../../assets/icons/game.svg';
import warningImg from '../../assets/icons/warning.svg';
import progressImg from '../../assets/icons/progress.svg';
import styles from './Main.module.css';
// import Video from '../../components/Video/Video';

const Main = (): JSX.Element => (
  <div className={styles.main}>
    <Container fluid>
      <img className={styles.mainScreenImg} src={mainScreenImg} alt=" -    RS LANG" />
      <hr className={styles.color} />
      {/* <h5 className={styles.margin}> ,  ,   </h5>
      <Video /> */}
      <h4 className={styles.margin}>   RS LANG</h4>
      <Container>
        <Row>
          <Col lg={3} md={6} sm={6} xs={6}>
            <FigureItem img={booksImg} text="  " />
          </Col>
          <Col lg={3} md={6} sm={6} xs={6}>
            <FigureItem img={gameImg} text="   " />
          </Col>
          <Col lg={3} md={6} sm={6} xs={6}>
            <FigureItem img={warningImg} text="  " />
          </Col>
          <Col lg={3} md={6} sm={6} xs={6}>
            <FigureItem img={progressImg} text="  " />
          </Col>
        </Row>
        <Row>
          <Col className={styles.games}>
            <Games />
          </Col>
        </Row>
      </Container>
    </Container>
  </div>
);

export default Main;
